@font-face {
  font-family: icomoon;
  src: url('fonts/icomoon.eot?8479k9');
  src:
    url('fonts/icomoon.eot?8479k9#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?8479k9') format('truetype'),
    url('fonts/icomoon.woff?8479k9') format('woff'),
    url('fonts/icomoon.svg?8479k9#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^='icon-'],
[class*=' icon-'] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: icomoon, sans-serif !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-logo::before {
  content: '\e900';
}
